body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --orange-soda:#F06543;
    --platinum:#E8E9EB;
    --alabaster:#E0DFD5;
    --onyx:#313638;
    --sandy-brown: #F09D51;
}

* {
    background: var(--alabaster);
}

a {
    background: none;
}

p {
    font-size: 18px;
    color: var(--orange-soda);
    font-weight: bold;
    background-color: var(--platinum);
    text-align: center;
}

h1 {
    margin: auto 30px;
    padding: 10px;
    color: var(--onyx);
    font-weight: bold;
}

#header {
    display: flex;
    margin-left: 5px;
}

#navigation {
    align-items: flex-end;
    font-size: 22px;
    text-decoration: none;
    align-content: center;
}

#navigation a {
    color: var(--orange-soda);
    padding-left: 1em;
    font-weight: bold;
    text-decoration: none;
    text-shadow: 1px 1px 2 px var(--onyx);
}

.card {
    background-color: var(--platinum);
    border-radius: 5px;
    height: 400px;
    margin: 2rem;
    position: relative;
    width: 400px;
    box-shadow: 0 2px 2px var(--onyx), 0 6px 12px var(--onyx);
}

.card .img-container {
    height: 75%;
    overflow: hidden;
    text-align: center;
    border-radius: 10px;
    background-color: var(--platinum);
}

.card .img-container img {
    width: 100%;
    padding: 25px;
    margin-bottom: 20px;
    background-color: var(--platinum);
}

.card .content {
    font-size: 18px;
    background-color: var(--platinum);
    color: var(--orange-soda);
    font-weight: bold;
    text-align: center;
}

#project-icon {
    background-color: var(--platinum);
    padding: 5px;
}

#topics {
    font-size: small;
    font-style: italic;
    color: var(--onyx);
}

#card-title {
    margin-top: 10px;
    color: var(--onyx);
    text-shadow: 1px 1px 6px var(--sandy-brown);
}

.wrapper {
    padding-top: 50px;
    justify-content: space-around;
    height: 100%;
    display: flex;
    overflow: auto;
    flex-flow: row wrap;
    padding: 10px;
    align-content: flex-start;
}

#footer {
    padding: 5px;
    margin-top: 10px;
}

.icon {
    padding: 10px;
}

#about-section {
    margin: 2em;
}

#about-section p {
    background-color: var(--alabaster);
    color: var(--onyx);
}

#contact-section {
    justify-content: center;
    margin: 20px;
    margin-left: 10em;
    margin-right: 10em;
}

#front-back ul {
    list-style-type: none;
}

#front-back ul li {
    list-style-type: none;
}

#front-back h2 {
    color: var(--onyx);
}

#front-back p {
    background-color: var(--alabaster);
}

#resume p {
    background-color: var(--alabaster);
    color: var(--onyx);
    margin-top: 5px;
}

#footer {
    background-color: var(--onyx);
}

#footer .icon {
    background-color: var(--onyx);
    margin-top: 20px;
}

#footer p {
    background-color: var(--onyx);
    margin-top: 20px;
}

#footer a {
    text-decoration: none;
    color: var(--orange-soda);
}

#react-icon {
    background-color: var(--onyx);
}
